    font-size: 0.875rem;
  }
  
  .grid-cols-3 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  
  .marquee, .marquee-reverse {
    animation-duration: 35s;
  }
  
  .animate-float {
    display: none;
  }
}

/* Overlay for mobile menu */
.mobile-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 40;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.mobile-overlay.active {
  opacity: 1;
  visibility: visible;
}

/* Utility classes for responsive behavior */
.responsive-text-6xl {
  font-size: 4rem;
  line-height: 1.1;
}

.responsive-text-2xl {
  font-size: 1.5rem;
}

@media (max-width: 1023px) {
  .responsive-text-6xl {
    font-size: 3rem;
  }
  
  .responsive-text-2xl {
    font-size: 1.25rem;
  }
}

@media (max-width: 767px) {
  .responsive-text-6xl {
    font-size: 2.5rem;
  }
  
  .responsive-text-2xl {
    font-size: 1rem;
  }
}

@media (max-width: 639px) {
  .responsive-text-6xl {
    font-size: 2rem;
  }
  
  .responsive-text-2xl {
    font-size: 0.875rem;
  }
}

/* Enhanced card responsiveness */
.responsive-card {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

@media (min-width: 1024px) {
  .responsive-card:hover {
    transform: translateY(-8px) scale(1.02);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  }
}

@media (max-width: 1023px) {
  .responsive-card:hover {
    transform: translateY(-4px) scale(1.01);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
}

/* Responsive spacing utilities */
.responsive-padding {
  padding: 2rem;
}

@media (max-width: 1023px) {
  .responsive-padding {
    padding: 1.5rem;
  }
}

@media (max-width: 767px) {
  .responsive-padding {
    padding: 1rem;
  }
}

@media (max-width: 639px) {
  .responsive-padding {
    padding: 0.75rem;
  }
}

.responsive-margin {
  margin: 2rem 0;
}

@media (max-width: 1023px) {
  .responsive-margin {
    margin: 1.5rem 0;
  }
}

@media (max-width: 767px) {
  .responsive-margin {
    margin: 1rem 0;
  }
}

@media (max-width: 639px) {
  .responsive-margin {
    margin: 0.75rem 0;
  }
}

/* Tablet and below: move .right-section to the bottom */
@media (max-width: 1023px) {
  .content-layout {
    flex-direction: column;
  }
  .main-content {
    order: 1;
  }
  .right-section {
    order: 2;
    margin-top: 2rem;
    margin-bottom: 0;
  }
}

/* Mobile: ensure .right-section is at the bottom */
@media (max-width: 767px) {
  .right-section {
    order: 2;
    margin-top: 1.5rem;
  }
}
@media (max-width: 639px) {
  .right-section {
    order: 2;
    margin-top: 1rem;
  }
}

/* Remove absolute positioning for .years-experience */
.years-experience {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: static;
  margin-top: 1rem;
  margin-bottom: 2rem;
  left: auto;
  bottom: auto;
}

/* Responsive adjustments if needed */
@media (max-width: 1023px) {
  .years-experience {
    justify-content: center;
    margin-top: 1rem;
    margin-bottom: 1.5rem;
  }
}
@media (max-width: 767px) {
  .years-experience {
    margin-top: 0.75rem;
    margin-bottom: 1rem;
  }
}

/* Get in Touch grid responsive fix - keep 3 columns on all screens */
.right-section .grid {
  grid-template-columns: repeat(2, minmax(0, 1fr)) !important;
  gap: 0.75rem !important;
}

@media (max-width: 480px) {
  .right-section .grid {
    grid-template-columns: repeat(2, minmax(0, 1fr)) !important;
    gap: 0.5rem !important;
  }
}

@media (max-width: 1023px) {
  .mobile-sidebar-fix {
    background: #fff !important;
    box-shadow: 0 4px 24px 0 rgba(0,0,0,0.08) !important;
    border-right: 2px solid #3b82f6 !important;
  }
}

